const filtersTranslate = {
  'group-accessories': {
    name: " "
  },
  "group-moto-models": {
    name: " ",
    "ural6736": {
      name: " 67-36"
    }
  },
  "group-useful-inf": {
    name: " "
  },
  "group-all": {
    name: " "
  }
}

let UserLinks = [
  { name: '', href: '/' },
  { name: ' ', href: '/info' },
  { name: ' ', href: '/service' },
]
let AdminLinks = [
  { name: ' ', href: '/admin/managment' },
  { name: '', href: '/admin/statistics' },
  { name: '', href: '/admin/session' }
]
let NavLinks

function App() {
  const [isAdminAuth, setIsAdminAuth] = useState(false)
  setInterval(() => {
    if (isAdminAuth) {
      setIsAdminAuth(false)
    }
  }, 900000)

  if (isAdminAuth)
    NavLinks = [{ name: '', href: '/' }, ...AdminLinks]
  else
    NavLinks = UserLinks

  return (
    <BrowserRouter>
      <div className="App teal">
        <Header props={{SITE_NAME, LINKS: NavLinks}}></Header>
        <main style={{minHeight: isAdminAuth ? '100vh' : '71.1vh'}}>
          <BaseRouter articlesLinksList={articlesLinksList} AdminAuth={{isAdminAuth, setIsAdminAuth}} filtersTranslate={filtersTranslate}/>
        </main>
        {!isAdminAuth ? <Footer props={{SITE_NAME}}></Footer> : null }
      </div>
    </BrowserRouter>
  );
}

export default App;
